import Vue from "vue";
import Vuex from "vuex";
import { loginApi, baseManagement } from "@/request/api";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // 登录人员信息
    CurrentUserInfo: {},
    // 不分页的物料详情表接口
    NoPagePersonMatterData: [],
    // 产品名称和内码接口
    ProductCodeNameList: [],
    // 产品名称和编码接口
    ProductIdNameList: [],
    // 产品 物料 名称和编码接口
    BomMatterCodeNameList: [],
    // 工站内码和工站名称接口：
    WorkCodeNameList: [],
    WorkCodeNameSearchList: [],
    // 人员内码和人员名称
    PersonCodeNameList: [],
    // 生产计划内码和名称接口：
    ProductPlanCodeNameList: [],
    // 物料内码和名称接口：
    MatterCodeNameList: [],
    // 工序内码和名称接口：
    ProcessCodeNameList: [],
    // 进行中的生产计划接口：
    ProductionPlanCode: "",
    // 不良原因code和name接口
    UnqualifiedResultCodeNameList:[],
    
    
  },
  mutations: {
    // 登录人员信息
    changeCurrentUserInfo(state, res) {
      state.CurrentUserInfo = res.data
    },
    // 不分页的物料详情表接口
    changeNoPagePersonMatterData(state, res) {
      state.NoPagePersonMatterData = res.data.data
    },
    // 产品名称和内码接口
    changeProductCodeNameList(state, res) {
      state.ProductCodeNameList = res.data
    },
    // 产品名称和编码接口
    changeProductIdNameList(state, res) {
      state.ProductIdNameList = res.data
    },
    // 产品 物料 名称和编码接口
    changeBomMatterCodeNameNameList(state, res) {
      state.BomMatterCodeNameList = res.data
    },
    // 工站内码和工站名称接口：
    changeWorkCodeNameList(state, res) {
      state.WorkCodeNameList = res.data
    },
    changeWorkCodeNameSearchList(state, res) {
      state.WorkCodeNameSearchList = res.data
    },
    // 人员内码和人员名称
    changePersonCodeNameList(state, res) {
      state.PersonCodeNameList = res.data
    },
    // 生产计划内码和名称接口：
    changeProductPlanCodeNameList(state, res) {
      state.ProductPlanCodeNameList = res.data
    },
    // 物料内码和名称接口：
    changeMatterCodeNameList(state, res) {
      state.MatterCodeNameList = res.data
    },
    // 工序内码和名称接口：
    changeProcessCodeNameList(state, res) {
      state.ProcessCodeNameList = res.data
    },

    // 不良原因code和name接口
    changeUnqualifiedResultCodeNameList(state, res) {
      state.UnqualifiedResultCodeNameList = res.data.data
    },

    // 进行中的生产计划接口：
    changeProductionPlanCode(state, res) {
      if(res.data.data&&res.data.data.length>0){

        state.ProductionPlanCode = res.data.data[0].product_plan_code
      }else{
        state.ProductionPlanCode = ""
      }
    },
  },
  actions: {

    // 登录人员信息
    async getLogisticCurrentUserInfo(context) {
      await loginApi.getLogisticCurrentUserInfo().then((res) => {
        if (res.code == 0) {
          context.commit('changeCurrentUserInfo', res)
        }
      });
    },
    // 不分页的物料详情表接口
    async getLogisticNoPagePersonMatter(context) {
      await baseManagement.getLogisticNoPagePersonMatter().then((res) => {
        if (res.code == 0) {
          context.commit('changeNoPagePersonMatterData', res)
        }
      });
    },
    // 产品名称和内码接口
    async getLogisticProductCodeName(context) {
      await baseManagement.getLogisticProductCodeName().then((res) => {
        if (res.code == 0) {
          context.commit('changeProductCodeNameList', res)
        }
      });
    },
    // 产品名称和编码接口
    async getLogisticGetProductId(context) {
      await baseManagement.getLogisticGetProductId().then((res) => {
        if (res.code == 0) {
          context.commit('changeProductIdNameList', res)
        }
      });
    },
    // 产品 物料 名称和编码接口
    async getLogisticBomMatterCodeName(context) {
      await baseManagement.getLogisticBomMatterCodeName().then((res) => {
        if (res.code == 0) {
          context.commit('changeBomMatterCodeNameNameList', res)
        }
      });
    },

    // 工序内码和名称接口：
    async getLogisticProcessNameCode(context) {
      await baseManagement.getLogisticProcessNameCode().then((res) => {
        if (res.code == 0) {
          context.commit('changeProcessCodeNameList', res)
        }
      });
    },
    // 不良原因code和name接口
    async getLogisticNopageWorkUnqualifiedResultName(context,params) {
      await baseManagement.getLogisticNopageWorkUnqualifiedResultName(params).then((res) => {
        if (res.code == 0) {
          context.commit('changeUnqualifiedResultCodeNameList', res)
        }
      });
    },
    // 进行中的生产计划接口：
    async getLogisticProductPlanDeal(context,) {
      await baseManagement.getLogisticProductPlanDeal({
        prodect_code: "",
        plan_name: "",
        plan_status: "进行中",
        page: 1,
        page_size: 10,
        total: 0,
      }).then((res) => {
        if (res.code == 0) {
          context.commit('changeProductionPlanCode', res)
        }
      });
    },
    // 工站内码和工站名称接口：
    async getLogisticWorkCodeName(context) {
      await baseManagement.getLogisticWorkCodeName().then((res) => {
        if (res.code == 0) {
          context.commit('changeWorkCodeNameList', res)
        }
      });
    },
    async getLogisticProjectSearchWorkcode(context, params) {
      await baseManagement.getLogisticProjectSearchWorkcode(params).then((res) => {
        if (res.code == 0) {
          context.commit('changeWorkCodeNameSearchList', res)
        }
      });
    },
    // 人员内码和人员名称
    async getLogisticPersonCodeName(context) {
      await baseManagement.getLogisticPersonCodeName().then((res) => {
        if (res.code == 0) {
          context.commit('changePersonCodeNameList', res)
        }
      });
    },
    // 生产计划内码和名称接口：
    async getLogisticProductPlanCodeName(context) {
      await baseManagement.getLogisticProductPlanCodeName().then((res) => {
        if (res.code == 0) {
          context.commit('changeProductPlanCodeNameList', res)
        }
      });
    },
    // 物料内码和名称接口：
    async getLogisticMatterNameCode(context) {
      await baseManagement.getLogisticMatterNameCode().then((res) => {
        if (res.code == 0) {
          context.commit('changeMatterCodeNameList', res)
        }
      });
    },
  },
  modules: {},
});
