import axios from 'axios'
import qs from 'qs'
import { Loading } from 'element-ui'
// console.log(qs.stringify(params))
let loadings
// axios.defaults.timeout = 3000 // 3s没响应则认为该请求失败
// axios.defaults.baseURL = 'http://10.0.20.103:8000/logistic' //本地
axios.defaults.baseURL = 'http://10.0.0.235:8000/logistic' //服务器
// axios.defaults.baseURL = 'http://47.100.48.210:8000/logistic' //服务器
axios.defaults.withCredentials = true

// http request 拦截器 所有请求发出前都需要执行以下代码
axios.interceptors.request.use(request => {
  // request.params = qs.stringify(request.params)
  // request.data = qs.stringify(request.data)
  console.log('request', request)

  if (request.params) {
    console.log(request.url,'请求参数  GET request.params',  request.params)
  } else {
    console.log(request.url,'请求参数 POST request.data', request.data)
  }

  if (request.params) {
    loadings = Loading.service()
  }
  return request
},
  error => {
    return Promise.reject(error)
  }
)

axios.interceptors.response.use(
  request => {
    if (loadings) {
      loadings.close()
    }
    return request
  },
  error => {
    if (loadings) {
      loadings.close()
    }
    return Promise.reject(error)
  }
)

/**
 * get方法，对应get请求
 * @param {String} url [请求的url地址]  
 * @param {Object} params [请求时携带的参数]
 */
export function get(url, params) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err.data)
    })
  })
}

/**
 * post方法，对应post请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function post(url, params) {
  return new Promise((resolve, reject) => {
    axios.post(url, params).then(res => {
      resolve(res.data)
    })
      .catch(err => {
        reject(err.data)
      })
  })
}

/**
 * put方法，对应put请求
 * @param {String} url [请求的url地址]  
 * @param {Object} params [请求时携带的参数]
 */
export function put(url, params) {
  return new Promise((resolve, reject) => {
    axios.put(url, {
      params: qs.stringify(params)
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err.data)
    })
  })
}

/**
 * delete方法，对应delete请求
 * @param {String} url [请求的url地址]  
 * @param {Object} params [请求时携带的参数]
 */
export function del(url, params) {
  return new Promise((resolve, reject) => {
    axios.delete(url, {
      params: params
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err.data)
    })
  })
}