<template>
  <div>
    <div class="header">
      <el-row>
        <el-col :span="12">
          <div>
            <el-breadcrumb
              separator-class="el-icon-arrow-right"
              v-if="this.$route.path != '/'"
            >
              <!-- <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item> -->
              <el-breadcrumb-item>首页</el-breadcrumb-item>
              <el-breadcrumb-item v-if="parentName">{{ parentName }}</el-breadcrumb-item>
              <el-breadcrumb-item>{{ name }}</el-breadcrumb-item>
            </el-breadcrumb>
            <el-breadcrumb separator-class="el-icon-arrow-right" v-else>
              <el-breadcrumb-item>首页</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
        </el-col>
        <el-col :span="12">
          <div style="text-align: right">
            <!-- <span class="headerIcon logoImg"> </span> -->
            <!-- <span class="headerIcon"> 无界工场(上海)软件科技有限公司 </span> -->
            <!-- <span class="headerIcon"> 无界工场(上海)软件科技有限公司 </span> -->
            <el-dropdown trigger="click">
              <span class="username">
                <i class="iconfont icon-icon-peoples"></i>

                <span
                  >欢迎您，{{CurrentUserInfo.user_name}}</span
                >
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>
                  <span class="dropdownSpan" @click="editPassword">
                    <i class="iconfont icon-rizhichakan"></i>修改密码</span
                  >
                </el-dropdown-item>
                <el-dropdown-item>
                  <span class="dropdownSpan" @click="logout">
                    <i class="iconfont icon-zhuxiao"></i>注销</span
                  ></el-dropdown-item
                >
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-col>
      </el-row>
    </div>

    <!-- 新增人员弹出层 -->
    <el-dialog
      title="修改密码"
      width="600px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-form-item label="工号" prop="user_id">
          <span>{{ dialogForm.user_id }}</span>
        </el-form-item>
        <el-form-item label="用户名" prop="user_name">
          <span>{{ dialogForm.user_name }}</span>
        </el-form-item>
        <el-form-item label="旧密码" prop="old_password">
          <el-input
            v-model="dialogForm.old_password"
            type="password"
          ></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="new_password">
          <el-input
            v-model="dialogForm.new_password"
            type="password"
          ></el-input>
        </el-form-item>
        <el-form-item label="确认新密码" prop="new_password_check">
          <el-input
            v-model="dialogForm.new_password_check"
            type="password"
          ></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { baseManagement } from "@/request/api";
export default {
  name: "CommonHeader",
  computed: {
    ...mapState(["CurrentUserInfo"]),
  },

  data() {
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.dialogForm.new_password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      breadList: [],
      parentName: "",
      name: "",
      dialogFlag: false,
      dialogForm: {
        old_password: "",
        new_password: "",
      },
      dialogFormBase: {
        old_password: "",
        new_password: "",
      },
      dialogFormRules: {
        old_password: [
          { required: true, message: "请输入旧密码", trigger: "blur" },
        ],
        new_password: [
          { required: true, message: "请输入新密码", trigger: "blur" },
        ],
        new_password_check: [
          { required: true, validator: validatePass2, trigger: "blur" },
        ],
      },
    };
  },
  watch: {
    $route() {
      this.getBreadcrumb();
    },
  },
  methods: {
    editPassword() {
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });

      this.dialogForm = {
        user_code: this.CurrentUserInfo.user_code,
        user_id: this.CurrentUserInfo.user_id,
        user_name: this.CurrentUserInfo.user_name,
      };
    },

    // 密码 修改
    getLogisticModifyPassword(params) {
      baseManagement.getLogisticModifyPassword(params).then((res) => {
        console.log("密码 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    dialogSave() {
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接
          console.log("修改人员", this.dialogForm);
          this.getLogisticModifyPassword(this.dialogForm);
        }
      });
    },
    logout() {
      sessionStorage.clear();
      this.$store.replaceState(
        Object.assign(
          {},
          this.$store.state,
          JSON.parse(
            JSON.stringify({
              // 登录人员信息
              CurrentUserInfo: {},
              // 不分页的物料详情表接口
              NoPagePersonMatterData: [],
              // 产品名称和内码接口
              ProductCodeNameList: [],
              // 产品名称和编码接口
              ProductIdNameList: [],
              // 产品 物料 名称和编码接口
              BomMatterCodeNameList: [],
              // 工站内码和工站名称接口：
              WorkCodeNameList: [],
              WorkCodeNameSearchList: [],
              // 人员内码和人员名称
              PersonCodeNameList: [],
              // 生产计划内码和名称接口：
              ProductPlanCodeNameList: [],
              // 物料内码和名称接口：
              MatterCodeNameList: [],
              // 工序内码和名称接口：
              ProcessCodeNameList: [],
            })
          )
        )
      );

      if (this.$route.fullPath.indexOf("userUrl") > 0) {
        this.$router.push({ path: "/" });
      } else {
        this.$router.push({ path: "/ManagerLogin" });
      }
      // this.
    },
    getBreadcrumb() {
      this.parentName = this.$route.meta.parentName;
      this.name = this.$route.meta.name;
    },
  },
  mounted() {
    console.log("CommonHeader  CurrentUserInfo ", this.CurrentUserInfo);
    this.getBreadcrumb();
  },
};
</script>

<style scoped lang="scss">
.header {
  line-height: 60px;

  border-bottom: 1px solid #ebebeb;
  // font-size: 16px;
  .el-breadcrumb {
    line-height: 60px;
  }
  .headerIcon {
    display: inline-block;
    padding-right: 20px;
  }
  .logoImg {
    width: 40px;
    height: 40px;
    background: url("../assets/images/logoImg.png") 100%;
    background-size: cover;
    vertical-align: middle;
  }
  .username {
    height: 100%;
    display: inline-block;
    cursor: pointer;
  }
}

.dropdownSpan {
  display: inline-block;
  width: 100%;
}
</style>
